import React from "react"
import Layout from "../components/layout"
import Card from "../components/Card"
import Posts from "../components/Posts"
import PostBlog from "../components/postsBlog"
import SEO from "../components/seo"
import {useStaticQuery, Link} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/Captura.png"
import foto2 from "../images/descargar_programa.png"
import foto3 from "../images/Any-Video-Converter-Freeware_1.png"


const IndexPage = () => {

    return (
        <>
            <Layout indexHeader={true} sideNavigation={false} sideNavigation={true} urlPage="/">
                <SEO title="Toda la información sobre las vitaminas"
                     meta="Toda la información que necesitas sobre las vitaminas. ¿Qué son? ¿Qué funciones hacen? ¿Síntomas de déficit de vitaminas?"
                     description="Toda la información que necesitas sobre las vitaminas. ¿Qué son? ¿Qué funciones hacen? ¿Síntomas de déficit de vitaminas?"
                     lang="es"/>
                <h2>Vitaminas y minerales</h2>
                <p>&iquest;Est&aacute;s consireaguiendo lo que necesitas?</p>
                <p>Las vitaminas y los minerales son nutrientes esenciales porque cumplen cientos de funciones en el cuerpo. Hay una l&iacute;nea muy fina entre obtener suficiente cantidad de estos nutrientes (lo cual es saludable) y obtener demasiados (lo cual puede terminar da&ntilde;&aacute;ndote). Llevar una dieta saludable sigue siendo la mejor manera de obtener las cantidades suficientes de las vitaminas y los minerales que necesitas.</p>
                <p></p>
                <h3>Nutrientes esenciales para el cuerpo</h3>
                <p>Cada d&iacute;a, tu cuerpo produce piel, m&uacute;sculo y hueso. Produce una rica sangre roja que lleva nutrientes y ox&iacute;geno a remotos puestos de avanzada, y env&iacute;a se&ntilde;ales nerviosas que saltan a lo largo de miles de kil&oacute;metros de caminos del cerebro y del cuerpo. Tambi&eacute;n formula mensajeros qu&iacute;micos que van de un &oacute;rgano a otro, emitiendo las instrucciones que ayudan a mantener la vida.</p>
                <p></p>
                <p>Pero para hacer todo esto, tu cuerpo requiere algunas materias primas. Estas incluyen al menos 30 vitaminas, minerales y componentes diet&eacute;ticos que tu cuerpo necesita pero que no puede fabricar por s&iacute; mismo en cantidades suficientes.</p>
                <p></p>
                <p>Las vitaminas y los minerales se consideran nutrientes esenciales, porque actuando en conjunto, desempe&ntilde;an cientos de funciones en el cuerpo. Ayudan a fortalecer los huesos, curar las heridas y reforzar el sistema inmunol&oacute;gico. Tambi&eacute;n convierten los alimentos en energ&iacute;a y reparan el da&ntilde;o celular.</p>
                <p></p>
                <p>Pero tratar de hacer un seguimiento de lo que hacen todas estas vitaminas y minerales puede ser confuso. Si lee suficientes art&iacute;culos sobre el tema, sus ojos pueden nadar con el alfabeto: las referencias de la sopa de letras a estos nutrientes, que se conocen principalmente por sus iniciales (como las vitaminas A, B, C, D, E y K, por nombrar s&oacute;lo algunas).</p>
                <p></p>
                <p>Aquí te intetaremos explicar mejor lo que estas vitaminas y minerales hacen realmente en el cuerpo y por qu&eacute; quieres asegurarte de que est&aacute;s consumiendo suficientes.</p>
                <p></p>
                <h3>Los micronutrientes con un gran papel en el cuerpo</h3>
                <p>Las vitaminas y los minerales a menudo se denominan micronutrientes porque el cuerpo s&oacute;lo necesita peque&ntilde;as cantidades de ellos. Sin embargo, el hecho de no obtener incluso esas peque&ntilde;as cantidades pr&aacute;cticamente garantiza la enfermedad. Aqu&iacute; hay algunos ejemplos de enfermedades que pueden ser el resultado de la deficiencia de vitaminas:</p>
                <p></p>
                <p>Escorbuto. Los marineros de anta&ntilde;o aprendieron que vivir durante meses sin frutas o vegetales frescos -las principales fuentes de vitamina C- causa el sangrado de las enc&iacute;as y la apat&iacute;a del escorbuto.</p>
                <p>La ceguera. En algunos pa&iacute;ses en desarrollo, la gente sigue quedando ciega por la deficiencia de vitamina A.</p>
                <p>Raquitismo. La deficiencia de vitamina D puede causar raquitismo, una enfermedad caracterizada por huesos blandos y d&eacute;biles que puede provocar deformidades esquel&eacute;ticas como piernas arqueadas. En parte para combatir el raquitismo, los Estados Unidos han fortificado la leche con vitamina D desde la d&eacute;cada de 1930.</p>
                <p>As&iacute; como la falta de micronutrientes clave puede causar un da&ntilde;o sustancial a su cuerpo, obtener cantidades suficientes puede proporcionar un beneficio sustancial. Algunos ejemplos de estos beneficios:</p>
                <p></p>
                <p>Huesos fuertes. Una combinaci&oacute;n de calcio, vitamina D, vitamina K, magnesio y f&oacute;sforo protege los huesos contra las fracturas.</p>
                <p>Previene los defectos de nacimiento. Tomar suplementos de &aacute;cido f&oacute;lico en las primeras etapas del embarazo ayuda a prevenir defectos de nacimiento en el cerebro y la columna vertebral de los hijos.</p>
                <p>Dientes saludables. El mineral fl&uacute;or no s&oacute;lo ayuda a la formaci&oacute;n de los huesos, sino que tambi&eacute;n evita que las caries dentales comiencen o empeoren.</p>
                <h2>La diferencia entre las vitaminas y los minerales</h2>
                <p>Aunque todos ellos se consideran micronutrientes, las vitaminas y los minerales difieren de manera b&aacute;sica. Las vitaminas son org&aacute;nicas y pueden ser descompuestas por el calor, el aire o el &aacute;cido. Los minerales son inorg&aacute;nicos y se aferran a su estructura qu&iacute;mica.</p>
                <p></p>
                <p>Entonces, &iquest;por qu&eacute; importa esto? Significa que los minerales del suelo y el agua se introducen f&aacute;cilmente en el cuerpo a trav&eacute;s de las plantas, los peces, los animales y los fluidos que se consumen. Pero es m&aacute;s dif&iacute;cil transportar las vitaminas de los alimentos y otras fuentes a tu cuerpo porque la cocci&oacute;n, el almacenamiento y la simple exposici&oacute;n al aire pueden inactivar estos compuestos m&aacute;s fr&aacute;giles.</p>
                <p></p>
                <p>Interactuar en las buenas y en las malas formas</p>
                <p>Muchos micronutrientes interact&uacute;an. La vitamina D permite que el cuerpo extraiga el calcio de las fuentes alimenticias que pasan por el tracto digestivo en lugar de recogerlo de los huesos. La vitamina C ayuda a absorber el hierro.</p>
                <p></p>
                <p>Sin embargo, la interacci&oacute;n de los micronutrientes no siempre es cooperativa. Por ejemplo, la vitamina C bloquea la capacidad del cuerpo para asimilar el mineral esencial cobre. E incluso una peque&ntilde;a sobrecarga del mineral manganeso puede empeorar la deficiencia de hierro.</p>
                <p></p>
                <p>Una mirada m&aacute;s cercana a las vitaminas solubles en agua</p>
                <p>Las vitaminas solubles en agua est&aacute;n empaquetadas en las porciones acuosas de los alimentos que comes. Se absorben directamente en el torrente sangu&iacute;neo cuando los alimentos se descomponen durante la digesti&oacute;n o cuando un suplemento se disuelve.</p>
                <p></p>
                <p>Debido a que gran parte de su cuerpo consiste en agua, muchas de las vitaminas solubles en agua circulan f&aacute;cilmente en su cuerpo. Los ri&ntilde;ones regulan continuamente los niveles de vitaminas solubles en agua, expulsando los excesos del cuerpo a trav&eacute;s de la orina.</p>
                <p></p>
                <h3>Vitaminas solubles en agua</h3>
                <ul>
                    <li>Vitaminas B</li>
                    <li>Biotina (vitamina B7)</li>
                    <li>&Aacute;cido f&oacute;lico (folato, vitamina B9)</li>
                    <li>Niacina (vitamina B3)</li>
                    <li>El &aacute;cido pantot&eacute;nico (vitamina B5</li>
                    <li>Riboflavina (vitamina B2)</li>
                    <li>Tiamina (vitamina B1)</li>
                    <li>Vitamina B6</li>
                    <li>Vitamina B12</li>
                    <li>La vitamina C</li>
                </ul>
                <p></p>
                <h3>Lo que hacen</h3>
                <p>Aunque las vitaminas solubles en agua tienen muchas tareas en el cuerpo, una de las m&aacute;s importantes es ayudar a liberar la energ&iacute;a que se encuentra en los alimentos que se consumen. Otras ayudan a mantener los tejidos sanos. Aqu&iacute; hay algunos ejemplos de c&oacute;mo las diferentes vitaminas ayudan a mantener la salud:</p>
                <p></p>
                <p><b>Liberar energ&iacute;a.</b> Varias vitaminas B son componentes clave de ciertas coenzimas (mol&eacute;culas que ayudan a las enzimas) que ayudan a liberar la energ&iacute;a de los alimentos.</p>
                <p><b>Producen energ&iacute;a.</b> La tiamina, la riboflavina, la niacina, el &aacute;cido pantot&eacute;nico y la biotina participan en la producci&oacute;n de energ&iacute;a.</p>
                <p><b>Construyen prote&iacute;nas y c&eacute;lulas.</b> Las vitaminas B6, B12 y el &aacute;cido f&oacute;lico metabolizan los amino&aacute;cidos (los bloques de construcci&oacute;n de las prote&iacute;nas) y ayudan a las c&eacute;lulas a multiplicarse.</p>
                <p><b>Producen col&aacute;geno.</b> Uno de los muchos papeles que desempe&ntilde;a la vitamina C es ayudar a producir col&aacute;geno, que une las heridas, apoya las paredes de los vasos sangu&iacute;neos y forma una base para los dientes y los huesos.</p>
                <p>Palabras al sabio</p>
                <p>Contrariamente a la creencia popular, algunas vitaminas solubles en agua pueden permanecer en el cuerpo durante largos per&iacute;odos de tiempo. Es probable que tengas un suministro de varios a&ntilde;os de vitamina B12 en tu h&iacute;gado. E incluso el &aacute;cido f&oacute;lico y las reservas de vitamina C pueden durar m&aacute;s de un par de d&iacute;as.</p>
                <p></p>
                <p>Sin embargo, en general, las vitaminas solubles en agua deben reponerse cada pocos d&iacute;as.</p>
                <p></p>
                <p>S&oacute;lo hay que tener en cuenta que existe un peque&ntilde;o riesgo de que el consumo de grandes cantidades de algunos de estos micronutrientes a trav&eacute;s de suplementos pueda ser bastante perjudicial. Por ejemplo, dosis muy altas de B6 -muchas veces la cantidad recomendada de 1,3 miligramos (mg) por d&iacute;a para los adultos- pueden da&ntilde;ar los nervios, causando entumecimiento y debilidad muscular.</p>
                <p></p>
                <h2>Vitaminas solubles en grasa</h2>
                <p>En lugar de deslizarse f&aacute;cilmente en el torrente sangu&iacute;neo como la mayor&iacute;a de las vitaminas hidrosolubles, las vitaminas liposolubles entran en la sangre a trav&eacute;s de los canales linf&aacute;ticos de la pared intestinal (v&eacute;ase la ilustraci&oacute;n). Muchas vitaminas solubles en grasa viajan por el cuerpo s&oacute;lo acompa&ntilde;adas por prote&iacute;nas que act&uacute;an como portadoras.</p>
                <p></p>
                <h3>Absorci&oacute;n de las vitaminas solubles en grasa</h3>
                <p></p>
                <p></p>
                <p>Se ingieren alimentos que contienen vitaminas liposolubles.</p>
                <p>El alimento es digerido por el &aacute;cido del est&oacute;mago y luego viaja al intestino delgado, donde se digiere m&aacute;s. La bilis es necesaria para la absorci&oacute;n de las vitaminas solubles en grasa. Esta sustancia, que se produce en el h&iacute;gado, fluye hacia el intestino delgado, donde descompone las grasas. Los nutrientes se absorben entonces a trav&eacute;s de la pared del intestino delgado.</p>
                <p>Una vez absorbidas, las vitaminas solubles en grasa entran en los vasos linf&aacute;ticos antes de llegar al torrente sangu&iacute;neo. En la mayor&iacute;a de los casos, las vitaminas solubles en grasa deben estar acopladas a una prote&iacute;na para poder viajar por el cuerpo.</p>
                <p>Estas vitaminas se utilizan en todo el cuerpo, pero los excesos se almacenan en el h&iacute;gado y en los tejidos grasos.</p>
                <p>A medida que se necesitan cantidades adicionales de estas vitaminas, el cuerpo aprovecha las reservas, liber&aacute;ndolas en el torrente sangu&iacute;neo desde el h&iacute;gado.</p>
                <p>Los alimentos y aceites grasos son reservas de las cuatro vitaminas solubles en grasa. Dentro de tu cuerpo, los tejidos grasos y el h&iacute;gado act&uacute;an como los principales reservorios de estas vitaminas y las liberan seg&uacute;n sea necesario.</p>
                <p></p>
                <p>Hasta cierto punto, puedes pensar en estas vitaminas como micronutrientes de liberaci&oacute;n gradual. Es posible consumirlas de vez en cuando, tal vez en dosis con semanas o meses de diferencia en lugar de diariamente, y a&uacute;n as&iacute; llenarse. Su cuerpo se deshace del exceso y lo distribuye gradualmente para satisfacer sus necesidades.</p>
                <p></p>
                <h3>Vitaminas solubles en grasa</h3>
                <ul>
                    <li>La vitamina A</li>
                    <li>La vitamina D</li>
                    <li>La vitamina E</li>
                    <li>La vitamina K</li>
                </ul>

                <h3>Lo que hacen</h3>
                <p>Juntos, este cuarteto de vitaminas ayuda a mantener en buen estado los ojos, la piel, los pulmones, el tracto gastrointestinal y el sistema nervioso. Aqu&iacute; est&aacute;n algunos de los otros papeles esenciales que juegan estas vitaminas:</p>
                <p></p>
                <p><b>Construir huesos.</b> La formaci&oacute;n de huesos ser&iacute;a imposible sin las vitaminas A, D y K.</p>
                <p><b>Proteger la visi&oacute;n.</b> La vitamina A tambi&eacute;n ayuda a mantener las c&eacute;lulas sanas y protege la visi&oacute;n.</p>
                <p><b>Interact&uacute;a favorablemente.</b> Sin la vitamina E, tu cuerpo tendr&iacute;a dificultades para absorber y almacenar la vitamina A.</p>
                <p><b>Protege el cuerpo.</b> La vitamina E tambi&eacute;n act&uacute;a como antioxidante (un compuesto que ayuda a proteger el cuerpo contra el da&ntilde;o de las mol&eacute;culas inestables).</p>
                <div style={{height: `300px`}}>
                </div>
            </Layout>
        </>
    )
}

export default IndexPage
